@use "base/variables" as v;
@use "base/mixins" as m;

.instituciones-logos {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
  padding: 1rem;

  @include m.telefono {
    grid-template-columns: repeat(2, 1fr);
    width: 80%;
    margin: inherit auto;
  }

  @include m.tablet {
    grid-template-columns: repeat(4, 1fr);
    width: 100%;
  }

  @include m.desktopXL {
    width: 80%;
    margin: inherit auto;
    grid-template-columns: repeat(4, 1fr);
  }

  .institucion {
    display: flex;
    justify-content: center;
    align-items: center;
    a {
      background-color: v.$blanco;
      border-radius: 50%;
      width: 14rem;
      height: 14rem;
      display: flex;
      justify-content: center;
      align-items: center;
      &.tec {
        padding: 3.5rem;
      }
      &.DH {
        padding: 3rem;
      }
      &.bedu {
        padding: 1rem;
      }
    }
    @include m.tablet {
      a {
        width: 15rem;
        height: 15rem;
        &.tec {
          padding: 5rem;
        }
        &.DH {
          padding: 4rem;
        }
        &.bedu {
          padding: 1rem;
        }
      }
    }
    @include m.desktop {
      a {
        width: 20rem;
        height: 20rem;
        &.tec {
          padding: 5rem;
        }
        &.DH {
          padding: 4rem;
        }
        &.bedu {
          padding: 1rem;
        }
      }
    }
  }

  img {
    width: 20rem;
  }
}
