@use "base/variables" as v;
@use "base/mixins" as m;

.hero .boton {
  width: auto;
  &:hover {
    color: inherit;
  }
}

.typing {
  width: 21ch;
  animation: typing 4s steps(19),
  blink .5s step-end infinite alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px solid;
  // font-family: monospace;
  // font-size: 2em;
}

@keyframes typing {
  from {
    width: 0;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}

.hero {
  background-image: url(../img/hero.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 22.5rem;
  position: relative;
  margin-bottom: 2rem;
  @include m.tablet {
    height: 25rem;
  }
  h2 {
    color: v.$blanco;
  }
}

.contenido-hero {
  padding: 5rem;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.6); /* Sintaxis anterior */
  background-color: rgb(0 0 0 / 70%);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  span {
    margin-top: 1rem;
    display: block;
    @include m.tablet {
      margin-top: 0;
      display: inline;
    }
  }
}

.contenido-hero p {
  color: v.$blanco;
  margin-top: 0;
}
