@use "base/variables" as v;
@use "base/mixins" as m;

.social-network {
  display: flex;
  justify-content: center;
  padding: 4rem inherit 2rem inherit;
}

.c-icon {
  width: 36px;
  height: 36px;
  fill: currentColor;
  transition: 0.2s;
}

.facebook-hover {
  fill: v.$disable;
  width: "48";
  height: "48";
  &:hover {
    fill: v.$facebook;
  }
}

// .facebook:hover  { fill: v.$facebook }
.c-link--googleMaps:hover {
  color: v.$googleMaps;
}
.c-link--whatsapp:hover {
  color: v.$whatsapp;
}
.c-link--instagram:hover {
  color: v.$instagram;
}
.c-link--facebook:hover::after {
  background: v.$facebook;
}
.c-link--googleMaps:hover::after {
  background: v.$googleMaps;
}
.c-link--whatsapp:hover::after {
  background: v.$whatsapp;
}
.c-link--instagram:hover::after {
  background: v.$instagram;
}

.c-link {
  position: relative;
  color: #bbb;
}

.c-link:not(:last-of-type) {
  margin-right: 5px;
  @include m.telefono {
    margin-right: 15px;
  }
}

.c-tooltip::before,
.c-tooltip::after {
  display: block;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -10px);
  opacity: 0;
  transition: 0.2s;
}

.c-tooltip::before {
  content: "";
  bottom: calc(100% + 2px);
  border: solid 5px transparent;
  border-top-color: currentColor;
}

.c-tooltip::after {
  content: attr(aria-label);
  bottom: calc(100% + 12px);
  padding: 0.61em 0.93em;
  font-size: 1.25rem;
  color: white;
  border-radius: 3px;
  font-weight: 800;
  @include m.desktop {
    font-size: 1.5rem;
  }
}

.c-tooltip:hover::before,
.c-tooltip:hover::after {
  opacity: 1;
  transform: translate(-50%);
}

.pens-link-container {
  flex-basis: 100%;
  font-size: 0.5rem;
  text-align: center;
}

.pens-link {
  margin-bottom: 0;
  background: white;
  box-shadow: 0 4px 18px rgba(0, 0, 0, 0.1);
  border: none;
}
